@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Roboto+Slab&display=swap);
.header {
    background-color: rgb(51, 51, 131);
    height: 300px;
    color: #fff;
}
.header-text {
    font-size: 45px;
    font-weight: bolder;
    letter-spacing: 3px;
    padding: 5.1rem 0 0 26.3rem;
}
.form {
    width: 38%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 40px auto;
}
.todo-form {
    width: 80%;
    height: 50px;
    padding: 10px;
    border-radius: 6px;
    font-size: 20px;
}
.todo-form:focus {
    outline: none;
}
.todo-form::-webkit-input-placeholder {
    font-size: 20px;
}
.todo-form:-ms-input-placeholder {
    font-size: 20px;
}
.todo-form::placeholder {
    font-size: 20px;
}
.todo-btn {
    width: 17%;
    border-radius: 6px;
    border: 0;
    background-color:rgb(110, 110, 245);
    height: 3rem;
}

/****** MEDIA QUERIES******/
@media screen and (max-width: 700px) {
    .header-text {
        padding: 5.1rem 0 0 0.3125rem ;
    }
    .form {
        width: 95%;
        padding: 5px;
        margin-top: 20px;
        margin-left: 0;
        
    }
}

.todo-list {
    width: 90%;
    background-color: #fff;
    margin: 0 auto;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.input-text {
    background-color: #fff;
    width: 80%;
    font-size: 20px;
}
.delete-btn, .edit-btn {
    float: right;
    margin-top: 6px;
}
.container {
    background-color: rgb(240, 157, 49);
    width: 38%;
    margin: -40px auto;
    border-radius: 10px;
    padding: 30px;
    margin-bottom: 5px;
}
.list-text {
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: bolder;
}
.input-checkbox {
    margin-top: 6px;  
}
.active {
    text-decoration: line-through;
    width: 80%;
    background-color: #fff;
    color: green;
    font-size: 20px;
}

/****** MEDIA QUERIES******/
@media screen and (max-width: 700px) {
    .container {
        width: 95%;
    }
    .todo-list {
        width: 100%;
    }
    .input-text {
        width: 60%;
    }
    .active {
        width: 60%;
    }
}


.footer {
    width: 38%;
    margin: 0 auto;
    background-color: #fff;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 3px 2px 3px 1px #888888;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 20px;
}
.items-left {
    color: #f00;
    font-size: 20px;
}
.total-items {
    color: rgb(51, 51, 131);
    font-size: 20px;
}
.items-completed {
    color: #0f0;
    font-size: 20px;
    cursor: pointer;
}

/****** MEDIA QUERIES******/
@media screen and (max-width: 700px) {
    .footer {
        width: 95%;
    }
    .items-left, .total-items, .items-completed {
        font-size: 18px;
    }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Akaya Telivigala', cursive;
}
